import axios from 'axios';

export default ({
    state: {
        PrevList: [],
    },
    getters: {
        PrevList: state => state.PrevList,

    },
    mutations: {
        updatePrevList (state, data) {
            state.PrevList.push(data)
        },
    },
    actions: {
        OpenEl (ctx, state) {
            let data = JSON.stringify({
                action: 'OpenEl',
                element: state
            });

            return new Promise((resolve, reject) => {
                axios.post(src+"/api.py", data)
                .then((response) => {
                    ctx.commit("markToken", null)
                    resolve(response.data.phrase)
                })
                .catch((error => {
                    console.log(error.statusText);
                }));
            });
        },
        PrevElproc (ctx, state) {
            let data = JSON.stringify({
                action: 'PrevElproc',
                element: state
            });

            return new Promise((resolve, reject) => {
                axios.post(src+"/api.py", data)
                .then((response) => {
                    ctx.commit("markToken", null)
                    resolve(response.data.phrase)
                })
                .catch((error => {
                    console.log(error.statusText);
                }));
            });
        },
        NextElproc (ctx, state) {
            let data = JSON.stringify({
                action: 'NextElproc',
                element: state
            });

            return new Promise((resolve, reject) => {
                axios.post(src+"/api.py", data)
                .then((response) => {
                    ctx.commit("markToken", null)
                    resolve(response.data.phrase)
                })
                .catch((error => {
                    console.log(error.statusText);
                }));
            });
        },
        NextEl (ctx, state) {
            let data = JSON.stringify({
                action: 'NextEl',
                element: state
            });

            return new Promise((resolve, reject) => {
                axios.post(src+"/api.py", data)
                .then((response) => {
                    ctx.commit("markToken", null)
                    resolve(response.data.phrase)
                })
                .catch((error => {
                    console.log(error.statusText);
                }));
            });
        }
    },
})