<template>
  <v-layout row>
    <v-flex xl6 offset-xl3 lg8 offset-lg2 md8 offset-md2 sm10 offset-sm1 xs12 mb-3>
      <v-card>
        <v-card-actions>

          <v-tooltip bottom>
            <template v-slot:activator="{ on }">
              <v-btn color="grey" fab dark v-on="on" @click="PrevElproc" class="action">
                <v-icon>skip_previous</v-icon>
              </v-btn>
            </template>
            <span>Предыдущий размеченный</span>
          </v-tooltip>

          <v-tooltip bottom v-model="prev_true ? '' : prev_true">
            <template v-slot:activator="{ on }">
              <v-btn color="grey" fab dark v-on="on" @click="PrevEl" v-bind:disabled="!prev_true"  class="action">
                <v-icon>chevron_left</v-icon>
              </v-btn>
            </template>
            <span>Предыдущий</span>
          </v-tooltip>

          <v-spacer></v-spacer>
          <v-text-field v-model="phrase_id" @keyup.enter="OpenEl" class="action"></v-text-field>
          <v-text-field disabled class="action" :label="`Разм. `+Phrase.processed_phrase"></v-text-field>
          <v-spacer></v-spacer>

          <v-tooltip bottom>
            <template v-slot:activator="{ on }">
              <v-btn color="grey" fab dark v-on="on" @click="NextEl" class="action">
                <v-icon>chevron_right</v-icon>
              </v-btn>
            </template>
            <span>Следующий</span>
          </v-tooltip>

          <v-tooltip bottom>
            <template v-slot:activator="{ on }">
              <v-btn color="grey" fab dark v-on="on" @click="NextElproc">
                <v-icon>skip_next</v-icon>
              </v-btn>
            </template>
            <span>Следующий размеченный</span>
          </v-tooltip>

        </v-card-actions>
      </v-card>
    </v-flex>
  </v-layout>
</template>

<script>
import { mapGetters } from "vuex";

export default {
  name: "NumberPhrase",
  data() {
    return {
      phrase_id: ""
    };
  },
  computed: {
    ...mapGetters(["Phrase", "ListSkills", "PrevList"]),
    prev_true() {
      if (this.PrevList.indexOf(this.phrase_id) === 0) {
        return false;
      } else {
        return true;
      }
    }
  },
  watch: {
    Phrase(n, o) {
      this.phrase_id = n.id;

      if (this.PrevList.indexOf(n.id) === -1) {
        this.$store.commit("updatePrevList", n.id);
      }
    }
  },
  methods: {
    PrevEl() {
      this.phrase_id = this.PrevList[this.PrevList.indexOf(this.phrase_id) - 1];
      this.$nextTick(() => {
        this.OpenEl();
      });
    },
    PrevElproc () {
        this.$store.dispatch('PrevElproc', this.phrase_id)
        .then(phrase => this.updateEl(phrase))
    },
    NextEl() {
      if (this.PrevList.length !== this.PrevList.indexOf(this.phrase_id) + 1) {
        this.phrase_id = this.PrevList[
          this.PrevList.indexOf(this.phrase_id) + 1
        ];
        this.$nextTick(() => {
          this.OpenEl();
        });
      } else {
        this.$store
          .dispatch("NextEl", this.phrase_id)
          .then(phrase => this.updateEl(phrase));
      }
    },
    NextElproc () {
        this.$store.dispatch('NextElproc', this.phrase_id)
        .then(phrase => this.updateEl(phrase))
    },
    OpenEl() {
      this.$store
        .dispatch("OpenEl", this.phrase_id)
        .then(phrase => this.updateEl(phrase));
    },
    updateEl(phrase) {
      this.$store.commit("updatePhrase", phrase);
      this.$store.commit("markFalseActiveListSkills");
      this.$store.commit("markActiveListSkills", phrase.skills);
    }
  }
};
</script>
