<template>
  <v-layout row>
    <v-flex xs8 offset-xs2 mt-3>
      <v-card>
        <v-card-actions>
          <v-tooltip bottom>
            <template v-slot:activator="{ on }">
              <v-btn
                :color="valid == false ? 'error' : color_valid"
                v-on="on"
                fab
                dark
                @click="valid = false"
                class="action"
              >
                <v-icon>thumb_down</v-icon>
              </v-btn>
            </template>
            <span>Не валидно</span>
          </v-tooltip>

          <v-tooltip bottom>
            <template v-slot:activator="{ on }">
              <v-btn
                :color="valid == true ? 'success' : color_valid"
                fab
                dark
                v-on="on"
                @click="valid = true"
              >
                <v-icon>thumb_up</v-icon>
              </v-btn>
            </template>
            <span>Валидно</span>
          </v-tooltip>

          <v-spacer></v-spacer>

          <v-tooltip bottom>
            <template v-slot:activator="{ on }">
              <v-btn color="grey" fab dark small v-on="on" @click="undo" class="action">
                <v-icon>undo</v-icon>
              </v-btn>
            </template>
            <span>Вернуть</span>
          </v-tooltip>

          <v-tooltip bottom>
            <template v-slot:activator="{ on }">
              <v-btn color="grey" fab dark small v-on="on" @click="nero_refresh" class="action">
                <v-icon>refresh</v-icon>
              </v-btn>
            </template>
            <span>Обновить NER</span>
          </v-tooltip>

          <v-tooltip bottom>
            <template v-slot:activator="{ on }">
              <v-switch v-on="on" v-model="check"></v-switch>
            </template>
            <span>{{ check ? 'Выключить' : "Включить" }} проверку</span>
          </v-tooltip>

          <v-spacer></v-spacer>

          <template v-if="!check">
            <v-tooltip bottom>
              <template v-slot:activator="{ on }">
                <v-btn color="primary" fab dark v-on="on" @click="savePhraseNext" class="action">
                  <v-icon>save</v-icon>
                </v-btn>
              </template>
              <span>Cледующий</span>
            </v-tooltip>

            <v-tooltip bottom>
              <template v-slot:activator="{ on }">
                <v-btn color="success" fab dark v-on="on" @click="savePhrase">
                  <v-icon>save</v-icon>
                </v-btn>
              </template>
              <span>Случайно</span>
            </v-tooltip>
          </template>

          <template v-else>
            <v-tooltip bottom>
              <template v-slot:activator="{ on }">
                <v-btn color="warning" fab dark v-on="on" @click="savePhraseNextCheck" class="action">
                  <v-icon>save</v-icon>
                </v-btn>
              </template>
              <span>Сохранить и перейти к следующему размеченному</span>
            </v-tooltip>
          </template>
        </v-card-actions>
      </v-card>
    </v-flex>
  </v-layout>
</template>

<script>
import { mapGetters } from "vuex";

export default {
  name: "Action",
  data() {
    return {
      color_valid: "grey",
      valid: null,
      check: false
    };
  },
  computed: {
    ...mapGetters(["Phrase", "ActiveSkills"])
  },
  watch: {
    Phrase(n, o) {
      if (n.valid === "True") {
        this.valid = true;
      } else if (n.valid === "False") {
        this.valid = false;
      } else {
        this.valid = null;
      }
    }
  },
  methods: {
    savePhraseNextCheck () {
      if (this.valid === null) {
        this.color_valid = "primary";
        setTimeout(() => {
          this.color_valid = "grey";
        }, 300);
        return;
      }

      if (this.valid === true && this.ActiveSkills.length == 0) {
        this.$store.commit("changeColorSkills", "primary");
        setTimeout(() => {
          this.$store.commit("changeColorSkills", "cyan");
        }, 300);
        return;
      }

      this.$store.dispatch("savePhrase", { valid: this.valid }).then(() => {
        this.valid = null;
        this.$store.dispatch("getListSkills");
        this.$store.dispatch("getNers");
        this.$store.dispatch("NextElproc", this.Phrase.id).then(phrase => {
          this.$store.commit("updatePhrase", phrase);
          this.$store.commit("markFalseActiveListSkills");
          setTimeout(() => {
            this.$store.commit("markActiveListSkills", phrase.skills);
          }, 20);
        });
      });

    },
    savePhraseNext() {
      if (this.valid === null) {
        this.color_valid = "primary";
        setTimeout(() => {
          this.color_valid = "grey";
        }, 300);
        return;
      }

      if (this.valid === true && this.ActiveSkills.length == 0) {
        this.$store.commit("changeColorSkills", "primary");
        setTimeout(() => {
          this.$store.commit("changeColorSkills", "cyan");
        }, 300);
        return;
      }

      this.$store.dispatch("savePhrase", { valid: this.valid }).then(() => {
        this.valid = null;
        this.$store.dispatch("getListSkills");
        this.$store.dispatch("getNers");
        this.$store.dispatch("NextEl", this.Phrase.id).then(phrase => {
          this.$store.commit("updatePhrase", phrase);
          this.$store.commit("markFalseActiveListSkills");
          setTimeout(() => {
            this.$store.commit("markActiveListSkills", phrase.skills);
          }, 20);
        });
      });
    },
    savePhrase() {
      if (this.valid === null) {
        this.color_valid = "primary";
        setTimeout(() => {
          this.color_valid = "grey";
        }, 300);
        return;
      }

      if (this.valid === true && this.ActiveSkills.length == 0) {
        this.$store.commit("changeColorSkills", "primary");
        setTimeout(() => {
          this.$store.commit("changeColorSkills", "cyan");
        }, 300);
        return;
      }

      this.$store.dispatch("savePhrase", { valid: this.valid }).then(() => {
        this.valid = null;
        this.$store.dispatch("getListSkills");
        this.$store.dispatch("getNers");
        this.$store.dispatch("getPhrase").then(phrase => {
          this.undo();
        });
      });
    },
    undo() {
      this.$store.dispatch("OpenEl", this.Phrase.id).then(phrase => {
        this.$store.commit("updatePhrase", phrase);
        this.$store.commit("markFalseActiveListSkills");
        this.$store.commit("markActiveListSkills", phrase.skills);
      });
    },
    nero_refresh() {
      this.$store.dispatch("nero_refresh", this.Phrase.id).then(phrase => {
        this.$store.commit("updatePhrase", phrase);
        this.$store.commit("markFalseActiveListSkills");
        this.$store.commit("markActiveListSkills", phrase.skills);
      });
    }
  }
};
</script>

<style>
.action {
  margin-right: 10px !important;
}
</style>